<template>
  <div class="hello">
    <div id="left">
      <h4>input</h4>
      <textarea name="input" id="input" v-model="input"></textarea>
    </div>
    <div id="right">
      <h4>output</h4>
      <textarea name="output" id="output" v-model="output"></textarea>
    </div>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  data: () => {
    return {
      input: ""
    };
  },
  computed: {
    output: {
      get: function() {
        let vm = this;
        return parse(vm.input);
      },
      set: function() {}
    }
  }
};
let parse = function(input) {
  let output = "";
  let scheme = [
    "轉診醫院",
    "診斷",
    "病房",
    "Birthday",
    "Date",
    "Age(Month)",
    "Name",
    "Hx-No",
    "(男)",
    "(女)",
    "Wt（kg）",
    "Nasal tract",
    "Pharynx",
    "Larynx",
    "Trachea",
    "Bronchus",
    "Esophagus",
    "Oto-tract",
    "Conclusions",
    "Suggestions",
    "診斷性",
    "治療性",
    "1.Scopy intubation",
    "2.Lavage",
    "3.Balloon",
    "4.Stent implantation",
    "5.Repair damage stent",
    "6.Granulation retrieval ",
    "7.Bronchial insufflation",
    "8.Foreign body removal",
    "9.Laser",
    "10.Other"
  ];
  let data = [];
  extract(input, "診斷", /PRIMARY DIAGNOSIS:\(.*\)/, data, scheme, x => {
    let result = "";
    result = x
      .replace("PRIMARY DIAGNOSIS:(", "")
      .replace(")", "")
      .replace(/\s+/g, " ")
      .trim();
    return result;
  });
  extract(input, "Name", /\d\S*\s\d{6,7}-\d/, data, scheme, x => {
    return x.match(/\D*\s/)[0].trim() || "";
  });
  extract(input, "Date", /簽收時間.*\d{8}/, data, scheme, x => {
    let datestr = x.match(/\d{8}/)[0] || "00000000";
    let result =
      datestr.slice(0, 4) +
      "/" +
      datestr.slice(4, 6) +
      "/" +
      datestr.slice(6, 8);
    return result;
  });
  extract(input, "Hx-No", /\d{6,7}-\d/, data, scheme);
  extract(input, "(男)", /\d{6,7}-\d\s+\S+/, data, scheme, x => {
    let result = x.replace(/\d{6,7}-\d\s+/, "") == "M" ? 1 : 0;
    return result;
  });
  extract(input, "(女)", /\d{6,7}-\d\s+\S+/, data, scheme, x => {
    let result = x.replace(/\d{6,7}-\d\s+/, "") == "F" ? 1 : 0;
    return result;
  });
  extract(input, "Wt（kg）", /BODY WEIGHT:\(.+\)/, data, scheme, x => {
    let result = x
      .replace(/BODY WEIGHT:\(/, "")
      .replace(")", "")
      .trim();
    return result;
  });
  // extract(input, "Nasal tract", /\d{6,7}-\d/, data, scheme);
  // extract(input, "Pharynx", /\d{6,7}-\d/, data, scheme);
  // extract(input, "Larynx", /\d{6,7}-\d/, data, scheme);
  // extract(input, "Bronchus", /\d{6,7}-\d/, data, scheme);
  // extract(input, "Esophagus", /\d{6,7}-\d/, data, scheme);
  // extract(input, "Oto-tract", /\d{6,7}-\d/, data, scheme);
  extract(
    input,
    "Conclusions",
    /CONCLUSIONS:\(.+\)\s+\(.+\)\s+\(.+\)/,
    data,
    scheme,
    x => {
      let result = x
        .replace(/CONCLUSIONS:\(/, "")
        .replace(/\s{3,}\(/g, "")
        .replace(/\s+\)/g, "")
        .replace(/\s+/g, " ")
        .trim();
      return result;
    }
  );
  extract(
    input,
    "Suggestions",
    /SUGGESTIONS:\(.+\)\s+\(.+\)/,
    data,
    scheme,
    x => {
      let result = x
        .replace(/SUGGESTIONS:\(/, "")
        .replace(/\s{3,}\(/g, "")
        .replace(/\s+\)/g, "")
        .replace(/\s+/g, " ")
        .trim();
      return result;
    }
  );
  scheme.forEach((e, i) => {
    output += (data[i] || "") + "\t";
  });
  return output;
};
let extract = function(input, col, re, data, scheme, map) {
  let index = -1;
  index = scheme.findIndex(x => x == col);
  if (index != -1) {
    let match = input.match(re);
    match = match && match[0];
    if (match) {
      if (map) {
        data[index] = map(match);
      } else {
        data[index] = match;
      }
    }
  }
};
</script>

